<template>
    <el-collapse v-model="activeNames" @change="handleChange" accordion id="blogitem">
        <el-collapse-item title="Java" name="1">
            <div class="block">
                <el-timeline color="rgb">
                    <el-scrollbar :height="scrollbarHeight">
                        <template v-for="(item, index) in blogitems" :key="index">
                            <el-timeline-item v-if="'Java' === (item.type)" placement="top" :timestamp="item.time">
                                <el-card style="background-color: rgba(245,253,255,0.3);" @click="gotoOneBlog(item.url)">
                                    <div class="block" style="height: 80%">
                                        <el-image style="width: 60%;" :src="item.backgroundImgUrl"></el-image>
                                    </div>
                                    <p>{{item.content}}</p>
                                </el-card>
                            </el-timeline-item>
                        </template>
                    </el-scrollbar>
                </el-timeline>
            </div>
        </el-collapse-item>
        <el-collapse-item title="Vue" name="2">
            <div class="block">
                <el-timeline color="rgb">
                    <el-scrollbar :height="scrollbarHeight">
                        <template v-for="(item, index) in blogitems" :key="index">
                            <el-timeline-item v-if="'Vue' === (item.type)" placement="top" :timestamp="item.time">
                                <el-card style="background-color: rgba(245,253,255,0.3);" @click="gotoOneBlog(item.url)">
                                    <div class="block" style="height: 80%">
                                        <el-image style="width: 60%;" :src="item.backgroundImgUrl"></el-image>
                                    </div>
                                    <p>{{item.content}}</p>
                                </el-card>
                            </el-timeline-item>
                        </template>
                    </el-scrollbar>
                </el-timeline>
            </div>
        </el-collapse-item>
        <el-collapse-item title="Study" name="3">
            <div class="block">
                <el-timeline color="rgb">
                    <el-scrollbar :height="scrollbarHeight">
                        <template v-for="(item, index) in blogitems" :key="index">
                            <el-timeline-item v-if="'Study' === (item.type)" placement="top">
                                <el-card style="background-color: rgba(245,253,255,0.3);" @click="gotoOneBlog(item.url)">
<!--                                    <div class="block" style="height: 80%">-->
<!--                                        <el-image style="width: 60%;" :src="item.backgroundImgUrl"></el-image>-->
<!--                                    </div>-->
                                    <p>{{item.content}}</p>
                                </el-card>
                            </el-timeline-item>
                        </template>
                    </el-scrollbar>
                </el-timeline>
            </div>
        </el-collapse-item>
    </el-collapse>
</template>

<script>
import _blogitems from "../../../textlibrary/blogitems";

export default {
    data() {
        return {
            scrollbarHeight: Number(`${document.documentElement.clientHeight}`) * 0.3,
            activeNames: ['1'],
            blogitems: [],
        };
    },
    created() {
        this.blogitems = _blogitems;
    },
    mounted() {
        const _this = this;
        window.onresize = function temp() {
            _this.scrollbarHeight = Number(`${document.documentElement.clientHeight}`) * 0.4;
        };
    },
    methods: {
        handleChange(val) {
            console.log(val);
        },
        gotoOneBlog(url){
            this.$router.push({name:url})
        }
    }
}
</script>
<style>
#blogitem div{
    background-color: rgba(245,253,255,0);
}
#blogitem .el-card__body{
    padding: 5px;
}
#blogitem .el-collapse-item__header{
    font-size: 15px;
}
#blogitem .el-timeline-item{
    padding-bottom: 5px;
}
</style>
