<script>
import md5 from 'js-md5'
import _dailyEvents from '../textlibrary/dailyEvents'

export default {
    name: 'login',
    data() {
        const urls = [
            require('../assets/advertisingimg/glasses/1.jpg'),
            require('../assets/advertisingimg/glasses/2.jpg'),
            require('../assets/advertisingimg/glasses/3.jpg'),
            require('../assets/advertisingimg/glasses/4.jpg'),
            require('../assets/advertisingimg/glasses/5.jpg'),
            require('../assets/advertisingimg/glasses/6.jpg'),
            require('../assets/advertisingimg/glasses/7.jpg'),
        ]
        const urls1 = [
            require('../assets/advertisingimg/glasses/11.jpg'),
            require('../assets/advertisingimg/glasses/12.jpg'),
            require('../assets/advertisingimg/glasses/13.jpg'),
            require('../assets/advertisingimg/glasses/14.jpg'),
            require('../assets/advertisingimg/glasses/15.jpg'),
            require('../assets/advertisingimg/glasses/16.jpg'),
            require('../assets/advertisingimg/glasses/17.jpg'),
            require('../assets/advertisingimg/glasses/18.jpg'),
            require('../assets/advertisingimg/glasses/19.jpg'),
        ]
        return {
            form: {
                username: "",
                password: ""
            },
            dailyEvents: [],
            dailyEventTitle: '',
            dailyEventContent: '',
            dailyEventUrl: '',
            urls,
            urls1,
        };
    },
    created() {
        this.dailyEvents = _dailyEvents;
    },
    methods: {
        onSubmit() {//maomao mimi
            // eslint-disable-next-line no-empty
            if("e2145387bcc7842edef4ccecc34b7da4" == md5(this.form.username) &&
                // eslint-disable-next-line no-empty
                "dde6ecd6406700aa000b213c843a3091" == md5(this.form.password)){
                this.$router.push({name:'Index', params:{username: this.form.username}})
            }
        },
        initDailyEvent(dailyEvent){
            this.dailyEventTitle = dailyEvent.title
            this.dailyEventContent = dailyEvent.content
            this.dailyEventUrl = dailyEvent.url
        }
    }
};
</script>

